import {Component, OnInit} from '@angular/core';
import {ActivatedRoute, Router, NavigationExtras} from '@angular/router';
import {AlertController, ToastController} from '@ionic/angular';
import {CartItem} from 'src/app/client1/models/cart-item.model';
import {Food} from 'src/app/client1/models/food.model';
import {SubFood} from 'src/app/client1/models/subFood.model';
import {CartService} from 'src/app/client1/services/cart.service';
import {FoodClientService} from 'src/app/client1/services/food-client.service';
import {SubFoodClientService} from '../../../services/sub-food-client.service';

@Component({
  selector: 'app-list-sub-food',
  templateUrl: './list-sub-food.page.html',
  styleUrls: ['./list-sub-food.page.scss'],
})
export class ListSubFoodPage implements OnInit {
  id: number;
  //foods: Food[] = [];
  //food: Observable<Food>;
  foood: Food;
  fod: Food[];
  message = '';
  //subFood?: SubFood[];
  //subFood: SubFood[];
  //food: Food[] = [];
  subFood: SubFood[] = [];
  sendId: any;

  subfood: SubFood;

  constructor(
    private activatedRoute: ActivatedRoute,
    private foodService: FoodClientService,
    private subFoodService: SubFoodClientService,
    private cartService: CartService,
    private router: Router,
    private route: ActivatedRoute,
    private alertCtrl: AlertController,
    private toastController: ToastController
  ) {
    this.id = +this.activatedRoute.snapshot.paramMap.get('id');
  }

  ngOnInit() {
    console.log('----->', this.id);
    // @ts-ignore
    this.getFoodById(this.id);

    this.getSubFoodsByParent(this.id);
  }

  getFoodById(id: number): void {
    this.foodService.getFoodsById(this.id)
      .subscribe(
        data => {
          this.foood = data;
          console.log('parentFood is: ', data);
        },
        error => {
          console.log(error);
        });
  }

  getSubFoodsByParent(id: number) {
    this.subFoodService.getAllSubFoodByFood(id)
      .subscribe(
        data => {
          this.subFood = data;
          console.log('list-sub-food', data);
        },
        error => {
          console.log(error);
        });
  }

  addToCart(id: number) {
   // this.router.navigate(['/client1/client/detail', id]);

    const navigationExtras: NavigationExtras = {
      state: {
        sendId: this.id
      }
    };
    this.router.navigate(['/client/detail', id], navigationExtras);
  }


  addItemToCart(id: number) {
    console.log('add to cart express worked',id);
  }

  addThisToCart(idd: number,title: string, pprice: number,pic: string ){
    const cartitem: CartItem = {
      id: idd,
      name: title,
      price: pprice,
      image: pic,
      quantity: 1,
    };

    this.cartService.addToCart(cartitem);
    this.showConfirmatioMsg(title);
  }

  async presentToast(message: string) {
    const toast = await this.toastController.create({
      message,
      duration: 2000, // 4000 ms (4 secondes)
      position: 'middle', // Position du toast (peut être 'top', 'middle' ou 'bottom' ou 'top')
      color: 'success'

    });
    toast.present();
  }

  showConfirmatioMsg(title: string) {

    this.presentToast(title +' a bien été ajouté à votre panier.');
  }
}
